.product-card-grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 20px 1fr 40px;
    gap: 10px;
    width: 660px;
    height: 280px;   
    margin: 50px auto 0;
    background-color: white;
    border-radius: 3px;
    padding: 20px;
}

.product-info{
    grid-area: 1 / 1 / 2 / 2;
    display: flex;
    justify-content: space-between;
}

.product-img-container{
    width: 213px;
    height: 160px;
    border-radius: 5px;
    overflow: hidden;
}

.product-name{
    text-transform: capitalize;
}

.product-img-container img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.product-img-container{
    grid-area: 2 / 1 / 3 / 2;
}

.product-options-container{
    grid-area: 2 / 2 / 3 / 4;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    height: 160px;
}

.add-cart-btn{
    grid-area: 3 / 1 / 4 / 1;
    background-color: var(--custom-green-1);
    border-radius: 3px;
    font-size: 0.875rem;
    font-weight: bold;
    color: white;
    transition: .3s ease-in-out;
}

.add-cart-btn:hover{
    font-size: 0.95rem;
}

@media (max-width: 700px){
    .product-card-grid{
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 20px 1fr 40px 40px;
        width: 90%;
        height: 390px;
    }

    .product-info{
        grid-area: 1 / 1 / 2 / 3;
    }
    
    .product-img-container{
        width: 100%;
        height: 100%;
        grid-area: 2 / 1 / 4 / 2;
    }

    .product-options-container{
        grid-area: 2 / 2 / 5 / 4;
        height: 100%;
    }

    .add-cart-btn{
        grid-area: 4 / 1 / 5 / 1;
    }
}